.Burger{
    width: 900px;
    margin: 0 auto;
    height: 700px;
    overflow: scroll;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
}

@media(min-width: 2000px) and(min-height: 700px){
    .Burger {
        width: 65%;
        height: 600px;
    }
}

@media(min-width: 1500px) and(min-height: 700px){
    .Burger {
        width: 65%;
        height: 500px;
    }
}

@media(min-width: 1000px) and(min-height: 700px){
    .Burger {
        width: 65%;
        height: 400px;
    }
}
@media(min-width: 500px) and(min-height: 401px){
    .Burger {
        width: 65%;
        height: 300px;
    }
}
@media(min-width: 500px) and(min-height: 400px){
    .Burger {
        width: 65%;
        height: 200px;
    }
}


